import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Login from "./Login";
import {
  BASE_ROUTE,

} from "./_main/routeConstants";

function App() {
  return (
    <Router>  
      <Routes>
        <Route path={BASE_ROUTE} element={<Login />} />
      </Routes>
    </Router>
  );
}

export default App;
